<template>
	<view class="notice" v-if="isShow">
		<view class="title">{{title}}</view>
		<view class="content">{{content}}</view>
		<view class="btns">
			<button class="cancelText" @tap="cancel">{{cancelText}}</button>
			<button class="confirmText" @tap="confirm">{{confirmText}}</button>
		</view>
	</view>
</template>

<script>
	export default{
		props:{
			title:{
				type:String
			},
			content:{
				type:String
			},
			cancelText:{
				type:String
			},
			confirmText:{
				type:String
			}
		},
		data(){
			return{
				isShow:false,
				SERVER:this.development
			}
		},
		methods:{
			showL(){
				this.isShow = true;
				// setTimeout(this.hide, 2000);
			},
			hide(){
				this.isShow = false;
				// this.remove();
			},
			cancel(){
				this.isShow = false;
				this.$emit('cancel');
			},
			confirm(){
				this.isShow = false;
				this.$emit('confirm');
			}
		}
	}
</script>

<style lang="scss">
	.notice{
		width: 600rpx;
		height: 460rpx;
		background:url('data:image/png;base64,') no-repeat top center;
		background-size:100% 100%;
		padding-top:230rpx;
		position:fixed;
		top:50%;
		left:50%;
		transform: translate(-50%,-50%);
		text-align: center;
		box-sizing: border-box;
		.title{
			color:#4AA5E1;
			font-size:28rpx;
			font-weight:bold;
		}
		.content{
			color:#999;
			font-size:24rpx;
			margin-top:20rpx;
		}
		.btns{
			width:260rpx;
			margin:60rpx auto 0;
			display:flex;
			justify-content: space-around;
			button{
				width:100rpx;
				height:44rpx;
				color:#fff;
				box-sizing: border-box;
				font-size:28rpx;
				padding:0 !important;
				text-align: center;
				line-height:44rpx;
			}
			.cancelText{
				background:#bbb;
			}
			.confirmText{
				background:#41A5FD;
			}
		}
	}
</style>
